/* styles/square.css */
.cell {
  height: 100px;
  width: 100px;
  color: #48CAE4;
  background-color: white;
  font-size: 50px;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.cell img {
  max-width: 100%;
  max-height: 100%;
}

@media (max-width: 1024px) {
  .cell {
    height: 80px;
    width: 80px;
    font-size: 40px;
  }
}

@media (max-width: 767px) {
  .cell {
    height: 60px;
    width: 60px;
    font-size: 30px;
  }
}

@media (max-width: 480px) {
  .cell {
    height: 50px;
    width: 50px;
    font-size: 25px;
  }
}

.cell:hover {
  background-color: rgb(237, 237, 237);
}
